.sidebar{
    position: fixed;
    top: 0;
    left: 0;
    height: 100%;
    width: 240px;
    background-color: #11101d;
    padding: 6px 14px;
    
}
.call{
    position: absolute;
    top: -150px;
    color: #fff;
    font-size: 30px;
    left: 230px;
}
